<template>
    <div>
        <h1 id="title" style="color:#111e2c;"><b style="color:#96ff54;">ADICIONAR</b> <b>FILME/SÉRIE/JOGO</b></h1>
        <div class="container form">
            <form @submit.prevent="addRun()">
                <div class="container ">
                    <div class="row">
                        <div class="col-sm-12">
                            <!-- NOME -->
                            <label for="name">NOME</label><br>
                            <input id="name" class="name" v-model="name" placeholder="Nome..." required>
                        </div>

                        <div class="col-sm-12">
                            <!-- IMAGEM -->
                            <label for="img">IMAGEM</label><br>
                            <input id="img" v-model="image" placeholder="Link..." required>
                        </div>

                        <div class="col-sm-12">
                            <!-- TIPO -->
                            <label for="type">TIPO</label><br>
                            <select id="type" v-model="type" style="width:300px; height:35px;">
                                <option value="filme">Filme</option>
                                <option value="serie">Série</option>
                                <option value="jogo">Jogo</option>
                            </select>

                        </div>



                    </div>
                </div>
                <button type="submit" value="Adicionar" class="btn btn-primary">ADICIONAR</button>
            </form>

        </div>
    </div>
</template>

<script>
    export default {
        data: function () {
            return {
                image: "",
                name: "",
                rating: "",
                movies: [],
                type: "filme",
            }

        },
        created() {
            if (localStorage.getItem("movies")) {
                this.$store.state.movies = JSON.parse(localStorage.getItem("movies"))
            }
            this.movies = this.$store.state.movies
        },
        methods: {
            getLastId() {
                return this.$store.getters.lastId
            },
            addRun() {
                this.$store.commit('ADD_MOVIE', {
                    id: this.getLastId(),
                    name: this.name,
                    image: this.image,
                    type: this.type
                })
                alert("ADICIONADO COM SUCESSO")
                this.name = ""
                this.image = ""
                this.type = "Filme"

            }
        }
    }
</script>

