        margin-bottom: 50px;
        border: solid 1px #A3A2A2;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    }

    #eventType {
        border: solid 1px #A3A2A2;
        width: 200px;
    }

    #link {
        color: #111e2c;
        font-weight: bold;
    }

    label {
        float: left;
        margin-left: 25px;
        margin-top: 20px;
        font-size: 20PX;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        font-weight: bold;
        color: #111e2c;
    }

    .btn {
        color: #111e2c;
        background-color: #96ff54;
